* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Karla, sans-serif;
  text-decoration: none;
  list-style: none;
}

body {
  background: #131b23;
}

section {
  width: 100%;
  height: 100%;
  display: flex;
  position: relative;
}

.container-image {
  width: 50%;
  height: 100%;
  position: relative;
}

.container-image .image {
  width: 100%;
  height: 660px;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
}

.container-image:before {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 2;
  mix-blend-mode: screen;
  position: absolute;
}

.container-form {
  width: 50%;
  height: 100%;
  width: 50%;
  height: 600px;
  justify-content: center;
  align-items: center;
  display: flex;
}

.container-form .form {
  width: 50%;
}

.container-form .form__title {
  color: #f64d33;
  font-weight: 1.5em;
  margin-top: 10px;
  margin-bottom: 20px;
  display: inline-block;
}

.container-form .form__item {
  margin-bottom: 20px;
}

span {
  color: #fff;
  letter-spacing: 1px;
  margin-bottom: 5px;
  font-size: 16px;
  font-weight: 300;
  display: inline-block;
}

input {
  width: 100%;
  color: #131b23;
  background: #fff;
  border: 1px solid #fff;
  border-radius: 10px;
  outline: none;
  padding: 10px 20px;
  font-size: 16px;
  font-weight: 400;
}

input[type="submit"] {
  color: #fff;
  cursor: pointer;
  background: #f64d33;
  border: none;
  outline: none;
  font-weight: 500;
}

input[type="submit"] :hover {
  background: #fa614a;
}

textarea {
  border-radius: 10px;
}

@media (max-width: 768px) {
  .container-image {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }

  .container-form {
    width: 100%;
    height: 100%;
    z-index: 1;
    justify-content: center;
    align-items: center;
    display: flex;
  }

  .form {
    width: 100%;
    background: #131b2371;
    border-radius: 10px;
    margin: 50px;
    padding: 40px;
  }
}

